export default {
	data() {
		return {
			searchStr: '',
			TabCur: 0,
			scrollLeft: 0,
			pageNum: 1,
			totalNum: 0,
			pageSize: 20,
			scenicList: [],
			navList: [
					{
				name: '推荐',
				value: '推荐'
			},{
				name: '山',
				value: '山'
			},{
				name: '水',
				value: '水'
			},{
				name: '园林',
				value: '园林'
			},{
				name: '人文',
				value: '人文'
			},{
				name: '休闲',
				value: '休闲'
			},{
				name: '地质地貌',
				value: '地质地貌'
			},{
				name: '宗教圣地',
				value: '宗教圣地'
			}]
		}
	},
	onShow: function(option) {
		this.recommend()
	},
	mounted() {
		// this.recommend()
	},
	methods: {
		toScenicDetail(item) {
			this.isLogin()
			uni.navigateTo({
				url: '../scenic_detail/scenic_detail?scenic_id='+item.id
			});
		},
		toYuyueDetail(item) {
			this.isLogin()
			uni.navigateTo({
				url: '../yuyue/yuyue?scenic_id=' + item.id
			});
		},
		isLogin(){
			if (!uni.getStorageSync('local_userinfo')) {
				uni.showToast({
					title: '请登录哦!',
					duration: 2000
				});
				uni.navigateTo({
					url: '../../base/login/login'
				})
			}
		},
		recommend() {
			var _this = this
			uni.getStorage({
				key: 'local_userinfo',
				success: function (res) {
					uni.request({
						url: _this.$BASE_HOST + 'scenic/recommend',
						method: 'GET',
						data: {
							user_id: res.data.id,
							pageNum: _this.pageNum,
							pageSize: _this.pageSize
						},
						success: (res) => {
							_this.scenicList = res.data.data.records
							_this.totalNum = res.data.data.total
							//scenicList 加工，图片增加host
							_this.scenicList.forEach((item) => {
								item.scenicCover = _this.$BASE_HOST + item.scenicCover
								item.scenicType = item.scenicType.split(',')
							})
						}
					});
				},fail: function (err) {
					uni.request({
						url: _this.$BASE_HOST + 'scenic/recommend',
						method: 'GET',
						data: {
							pageNum: _this.pageNum,
							pageSize: _this.pageSize
						},
						success: (res) => {
							_this.scenicList = res.data.data.records
							_this.totalNum = res.data.data.total
							//scenicList 加工，图片增加host
							_this.scenicList.forEach((item) => {
								item.scenicCover = _this.$BASE_HOST + item.scenicCover
								item.scenicType = item.scenicType.split(',')
							})
						}
					});
				}
			});
		},
		searchByNav(nav) {
			if (nav === '推荐') {
				this.recommend()
				return
			}

			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'scenic/searchByType',
				method: 'get',
				data: {
					'type': nav
				},
				success: (res) => {
					_this.scenicList = res.data.data.rows
					_this.totalNum = res.data.data.total
					//scenicList 加工，图片增加host
					_this.scenicList.forEach((item) => {
						item.scenicCover = _this.$BASE_HOST + item.scenicCover
						item.scenicType = item.scenicType.split(',')
					})
				}, fail() {
					uni.showToast({
						title: '服务异常',
						duration: 2000,
						icon: 'none'
					});
				}
			})
		},
		search() {
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'scenic/search',
				method: 'get',
				data: {
					'matchStr': this.searchStr
				},
				success: (res) => {
					_this.scenicList = res.data.data.rows
					_this.totalNum = res.data.data.total
					//scenicList 加工，图片增加host
					_this.scenicList.forEach((item) => {
						item.scenicCover = _this.$BASE_HOST + item.scenicCover
						item.scenicType = item.scenicType.split(',')
					})
				}, fail() {
					uni.showToast({
						title: '服务异常',
						duration: 2000,
						icon: 'none'
					});
				}
			})
		},
		searchButton() {
			this.searchScenicFromServer(this.searchStr)
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			// this.search(e.detail.value)
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		navSelect(item) {
			console.log(item)
			this.searchByNav(item.name)
		}
	}
}
